#content--left,
#content--right {
  &.mobile-collapsible {
    max-height: 25px;
    overflow: hidden;
    transition: all 0.4s;
    background-color: $secondaryColor;
    margin-bottom: 0.375em;
    &>* {
      box-sizing: border-box;
    }
    h4 {
      position: relative;
      margin: 0;
      padding: 5px;
      color: $tertiaryColor;
      &:after {
        content: "+";
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        border: 1px solid $bgColor;
        color: $bgColor;
        border-radius: 50%;
        height: 1em;
        width: 1em;
        line-height: 1em;
        font-size: 1em;
        text-align: center;
        opacity: 0.5;
        cursor: pointer;
      }
    }
    &:hover h4::after {
      opacity: 1;
    }
    &.open {
      z-index: 9;
      max-height: 500px;
      transition: all 0.2s, max-height 1.8s;
    }
    &.open h4:after {
      content: "-";
    }
    .content-side-container div {
      padding: 10px;
      color: #fff;
    }
  }
}