body {
  background-color: $bgColor;
  color: $textColor;
  font-family: 'Merriweather', serif;
}
a {
  text-decoration: none;
}

#header-container {
  display: flex;
  justify-content: center;
  h2 {
    margin: $size;
    font-family: 'Permanent Marker', cursive;
    background: linear-gradient(to bottom, rgba(207,223,233,1) 0%, rgba(20,125,151,1) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    position: relative;
    &:before {
      content: url(../img/logo-icon.png);
      position: absolute;
      right: 102%;
    }
  }
}

#nav-container {
  .nav {
    display: flex;
    list-style: none;
    justify-content: space-evenly;
    -webkit-margin-before: 0;
    -webkit-margin-after: 0;
    -webkit-padding-start: 0;
    .nav--item {
      border-top: 2px solid;
      flex-grow: 1;
      text-align: center;
      padding-top: 5px;
      border-color: $tertiaryColor;
      &.active {
        border-color: $secondaryColor;
        a {
        color: $mainColor;
        }
      }
      a {
        display: inline-block;
        height: 100%;
        width: 100%;
        font-family: sans-serif;
        color: $secondaryColor;
      }
      &:hover {
        border-color: $mainColor;
        a {
          color: $mainColor;
        }
      }
    }
  }
}

#content {
  margin: $size;
  border-top: 1px solid #ddd;
  min-width: 20em;
  display: grid;
  grid-template-areas: 
                      "top"
                      "mid"
                      "bot";
  // grid-template-columns: 1fr 3fr 1fr;
  column-gap: 10px;
  >* {
  }
  .entry {
    padding: 5px 0 5px 10px;
    box-shadow: inset 2px 2px 3px 0px #9EBECF;   
    // grid-area: top bot; 
    p:first-of-type:first-letter {
      font-size: 4em; 
      line-height: 1; 
      float: left; 
      font-weight: bold; 
      padding: 0 5px;
      color: $mainColor;
      border: 1px dotted $mainColor;
    }
    img {
      max-width: 100%;
      display: block;
      margin: 0 auto;
    }
  }
  .entry + .entry {
    margin-top: 1.375em;

  }
  #content--left,
  #content--right {
    font-size: 0.875em;
    color: #666;
    // padding-top: 5px;
  }
  #content--left {
    grid-area: top;
    img {
      display: none;
    }
  }
  #content--right {
    grid-area: mid;
    img {
      display: none;
    }
  }
  #content--center {
    grid-area: bot;
  }
}


#footer-container {
  background-color: $mainColor;
  padding: $size;
  color: #fff;
  text-align: center;
  font-size: 10px;
  a {
    text-decoration: none;
    color: #fff;
  }
}
